	color: #f5127b;
}

.avatar-red {
	background-color: #f0bcbc;
	color: $danger;
}

.avatar-orange {
	background-color: #fee0c7;
	color: #e67605;
}

.avatar-yellow {
	background-color: #fbedb7;
	color: $yellow;
}

.avatar-green {
	background-color: #cfeab3;
	color: $success;
}

.avatar-teal {
	background-color: #bfefea;
	color: $teal;
}

.avatar-cyan {
	background-color: #b9e3ea;
	color: #17a2b8;
}

.avatar-white {
	background-color: $white;
	color: $white;
}

.avatar-gray {
	background-color: #dbdde0;
	color: #868e96;
}

.avatar-gray-dark {
